import React, {Component} from 'react';
import axios from 'axios';
import Form from './Form';
import Members from './Members';

class Registrasi extends Component {
	constructor (props) {
  super(props)
  this.state = {
    first_name: '',
    email: '',
    nama: '',
    email1: '',
    members:[],
    formStatus: 'create',
      memberIdSelected: null
  }
}

onSubmitHandler = (e) => {
  e.preventDefault()
  var payload = {
      first_name: this.state.first_name,
      email: this.state.email
    }
    var url = 'https://reqres.in/api/users'
    axios.post(payload)
    .then(response => {
        var members = [...this.state.members]
          members.push(response.data)
          this.setState({ buttonDisabled: false, first_name: '',  email: '', nama:response.data.first_name, email1:response.data.email})
      })
    .catch(error => {
        console.log(error)
    })
}

addMember = (payload) => {
  
}

inputOnChangeHandler = (event) => {
  this.setState({ [event.target.name] : event.target.value})
}
	render(){
		return (
	<div className="container">
		<h1 style={{ textAlign: 'center'}}>FORM REGISTRASI</h1>
		<div className="col-md-12" style={{ marginTop: 40, marginBottom: 100}}>
		<Form
                      onSubmitForm={this.onSubmitHandler}
                      onChange={this.inputOnChangeHandler}
                      first_name={this.state.first_name}
                      email={this.state.email}
                      buttonDisabled={this.state.buttonDisabled}/>
		</div>
		<div className="row">
                    <Members
                        nama={this.state.nama}
                      email1={this.state.email1}/>

                </div>

	</div>
	)
}
}

export default Registrasi