import React from 'react';

const styles = {
	container: {
		position: 'relative',
		textAlign: 'center'
	},
	imageCover: {
		width: '100%'
	},
	blockText: {
		width: '100%',
		position: 'absolute',
		top: '50%',
		left: '50%',
		transform: 'translate(-50%, -50%)',
		color: 'white'
	}
}
const Home = () => (
	<div style={styles.container}>
		<img 
				style={styles.imageCover}
				src="https://www.ecopetit.cat/wpic/mpic/55-551009_programming-laptop-wallpapers-hd.jpg"
				alt="belajarcoding"
		/>
		<div style={styles.blockText}>
			
			<h1>Belajar Coding Sekarang</h1>
			<h4>Lebih dari 2.000 modul belajar pemrograman dalam beragam format yang bisa kamu pelajari</h4>
		</div>
	</div>
)

export default Home