import React from 'react';
import axios from 'axios';

const Form = (props) => (

	<form onSubmit={props.onSubmitHandler}>
        <div className="form-group">
            <label>Nama</label>
            <input type ="text" className="form-control" name="first_name" onChange={props.inputOnChangeHandler}/>
        </div>
        
        <div className="form-group">
            <label>E-Mail</label>
            <input type="text" className="form-control" name="email" onChange={props.inputOnChangeHandler}/>
        </div>
        <button type="submit" className="btn btn-primary" disabled={props.buttonDisabled}>Submit</button>
    </form>

	)

export default Form