import React from 'react';
import Intro from './Intro';
import Card from './Card';

const intro = {
	content: `As architects, we need to know a lot of stuff. We need to know building codes, structures, mechanical systems, materials. We need to know how to read zoning codes, how to calculate building area, how to layout office floor. The list goes on and on. Do we really need to know how to write computer programs as well?`
}

const cards = [
	{
		imageUrl: 'https://www.codepolitan.com/sites/codepolitan/themes/belajarcoding/assets/img/thumb-weekend.jpg',
		title: 'Weekend Bootcamp',
		subtitle: 'We all learn to write in school, but not everyone goes on to be a professional writer.'
	},
	{
		imageUrl: 'https://www.codepolitan.com/sites/codepolitan/themes/belajarcoding/assets/img/thumb-intensive.jpg',
		title: 'Intensive Bootcamp',
		subtitle: 'Not everyone wants to become a full-time programmer but learning some code can help you solve the kind of problems you face everyday.'
	}
]

const DevSchool = () => (
	<div className="container">
		<h1 style={{textAlign: 'center'}}>DEVELOPER SCHOOL</h1>
		<div className="row" style={{marginTop: 40, marginBottom: 100}}>
			<Intro content={intro.content}/>
			<div className="col-md-6">
				<img
					src=""
					alt=""

				/>
			</div>
		</div>
		<div className="row" style={{ marginBottom: 50}}>
			{
				cards.map(card => 
					<Card
						imageUrl={card.imageUrl}
						title={card.title}
						subtitle={card.title}
					/>
				)
			}
		</div>
	</div>
)

export default DevSchool