import React from 'react';

const members = (props) => (
	<form >
        <div className="form-group">
            <label>Nama</label>
            <input type ="text" className="form-control" name="nama" value={props.nama} disabled/>
        </div>
        
        <div className="form-group">
            <label>E-Mail</label>
            <input type="text" className="form-control" name="email1" value={props.email1} disabled/>
        </div>
        </form>


	)

export default members