import React from 'react';

const quran = (props) => (
	<div className="col-md-12" key={props.quran.nomor}>
            <div className="card" style={{margin: 10}}>
              <div className="card-body">
                  <h5>{props.quran.nomor}</h5>
                  <h5 className="card-title">{props.quran.nama}</h5>
                  <h5 className="card-title">{props.quran.arti}</h5>
                  <h5>{props.quran.ayat}</h5>
                  <h5>{props.quran.keterangan}</h5>
                  <a href={props.quran.audio}>Dengarkan Ya</a>

               </div>
          </div>
          </div>

)

export default quran