import React from 'react';
import Intro from './Intro';
import Card from './Card';

const intro = {
	content: `Given the digital nature of architecture and design today, learning to code is an essential skill for the architect. Architects are knowledge workers. Virtually all of our work is created on the computer.  We typically use off-the-shelf applications to do the majority of our work. However, many of the problems we need to solve don’t fall within the abilities of our software. Our applications don’t always work well together. We need tools that work the way we need them to work.`
}

const features = [
	{title: 'Materi Belajar', desc: 'You don’t need to become a professional programmer to benefit from learning to code.'},
	{title: 'Waktu Belajar', desc: 'If you need a very specific tool for your work, you can write a bit of code to create it.'},
	{title: 'Metode Belajar', desc: 'By working lazy, I don’t mean sitting with your feet up, sipping a beer while watching videos on YouTube. '}
]

const cards = [
	{title: 'Menjadi Web Programmer', imageUrl: 'https://www.codepolitan.com/sites/codepolitan/themes/belajarcoding/assets/img/thumb-weekend.jpg'},
	{title: 'Belajar Ngoding', imageUrl: 'https://www.codepolitan.com/sites/codepolitan/themes/belajarcoding/assets/img/thumb-intensive.jpg'}
]

const styles = {
	box450border: {
		height: '450px',
		border: '2px solid #f1f1f1',
		borderRadius: 10,
		padding: 30
	}
}

const Premium = () => (
	<div className="container">
		<h1 style={{ textAlign: 'center'}}>PREMIUM ONLINE COURSE</h1>
		<div className="row" style={{ marginTop: 40, marginBottom: 100}}>
			<Intro content={intro.content}/>
			<div className="col-md-6" style={styles.box450border}>
				{
					features.map(feature => (
						<div>
							<h5><strong>{feature.title}</strong></h5>
							<p>{feature.desc}</p>
						</div>
					))
				}
			</div>
		</div>
		<div className="row" style={{marginBottom: 50}}>
			{
				cards.map(card =>
					<Card
						imageUrl={card.imageUrl}
						title={Card.title}
					/>
				)
			}
		</div>
	</div>
)

export default Premium